:root {
  --transition-timing: cubic-bezier(.4, 0, .2, 1);
  --bgc-primary: #fefefe;
  --contour-img: #ffe550;
  --contour-img-hover: #bbf330;
  --btn-burger: #7eb101;
  --btn-burger-hover: #ffe550;
  --btn-bgc-first: #bbf330;
  --btn-bgc-first-hover: #b5ff00;
  --btn-bgc-second: #bbf330;
  --btn-bgc-second-hover: #bbf330;
  --btn-bgc-third: #fff;
  --btn-bgc-third-hover: #ffe550;
  --btn-bgc-fourth: #bbf330;
  --btn-bgc-fourth-hover: #b5ff00;
  --border-btn-order: #597d00;
  --title-color: #2c2c2c;
}

.container {
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  overflow: hidden;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

.header {
  background-color: var(--bgc-primary);
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
  z-index: 100;
  width: 100vw;
  padding: 10px 0;
  position: fixed;
  top: 0;
  left: 0;
}

.header__thumb {
  justify-content: space-between;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.nav {
  display: none;
}

@media screen and (min-width: 768px) {
  .nav {
    margin-left: auto;
    display: flex;
  }
}

.nav__list {
  gap: 28px;
  display: flex;
}

.nav__item {
  cursor: pointer;
  -webkit-text-decoration-line: none;
  text-decoration-line: none;
  transition: color .2s linear;
}

.nav__item:hover, .nav__item:focus {
  color: #bbf330;
}

.nav__item:active {
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.btn-buy {
  height: 32px;
  width: 98px;
  cursor: pointer;
  background-color: var(--btn-bgc-first);
  transition-property: background-color;
  transition-duration: .3s;
  transition-timing-function: var(--transition-timing);
  border: 0;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
  display: flex;
}

.btn-buy:hover, .btn-buy:focus {
  background-color: var(--btn-bgc-first-hover);
}

.header .btn-buy {
  margin-left: 30px;
  display: none;
}

@media screen and (min-width: 768px) {
  .header .btn-buy {
    display: flex;
  }
}

.header-burger__btn {
  width: 28px;
  height: 28px;
  color: var(--btn-burger);
  transition-property: color;
  transition-duration: .3s;
  transition-timing-function: var(--transition-timing);
  background-color: #fff;
  border: 0;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.header-burger__btn:hover, .header-burger__btn:focus {
  color: var(--btn-burger-hover);
}

@media screen and (min-width: 768px) {
  .header-burger__btn {
    display: none;
  }
}

.menu-container {
  width: 130px;
  z-index: 9999;
  background-color: var(--bgc-primary);
  border-radius: 30px 0 30px 30px;
  transition: transform .25s ease-in-out;
  position: absolute;
  top: 50px;
  right: 20px;
  transform: translateX(130%);
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

@media screen and (min-width: 768px) {
  .menu-container {
    display: none;
  }
}

body .menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu__item {
  padding: 16px;
}

.mobile-menu__item:first-child {
  border-top-left-radius: 30px;
}

.mobile-menu__item:last-child {
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.mobile-menu__item:hover, .mobile-menu__item:focus {
